.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  background-color: pink;
  display:flex;
  align-items:center;
  justify-content: center;
  padding:15%
}
.container{
  padding: 10px;
  border:  5px solid whitesmoke;
  width: 500px;
  border-radius: 16px;
  display: flex;
  flex-direction:column;
  color:white
}
.head{
  font-weight: 600;
  font-size:20px;
  margin-top: 20px;
}
input{
  padding: 10px;
  border:1px solid black;
  border-radius: 10px;
}
input:hover{
  background-color: rgb(196, 231, 232);
}
